import React from 'react';
import emailjs from 'emailjs-com';
import { Form, Input, TextArea, Button } from 'semantic-ui-react';
import Swal from 'sweetalert2';
import 'semantic-ui-css/semantic.min.css';

const SERVICE_ID = 'service_udze42g';
const TEMPLATE_ID = 'template_jy2mdoe';
const USER_ID = 'user_1ExjuviuReMy27DNwVwWZ';

const Contacts = () => {

    const handleOnSubmit = (e) => {
        e.preventDefault();        
        emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID)
          .then((result) => {
            console.log(result.text);
            Swal.fire({
              icon: 'success',
              title: 'Message Sent Successfully'
            })
          }, (error) => {
            console.log(error.text);
            Swal.fire({
              icon: 'error',
              title: 'Ooops, something went wrong',
              text: error.text,
            })
          });
        e.target.reset()
    };

    return (
        <div className="page" id="contact-me">
            <div className="container">
                <div className="heading">
                    <h2><span className="page-title">Get in Touch!</span></h2>
                    <p><span>If you would like to reach out, get in touch using the form below!</span></p>
                </div>
                <div className="contact-form">
                    <Form onSubmit={handleOnSubmit}>
                        <Form.Field
                            id='form-input-control-email'
                            control={Input}
                            label='Email'
                            name='user_email'
                            placeholder='Email…'
                            required
                            icon='mail'
                            iconPosition='left'
                        />
                        <Form.Field
                            id='form-input-control-last-name'
                            control={Input}
                            label='Name'
                            name='user_name'
                            placeholder='Name…'
                            required
                            icon='user circle'
                            iconPosition='left'
                        />
                        <Form.Field
                            id='form-textarea-control-opinion'
                            control={TextArea}
                            label='Message'
                            name='user_message'
                            placeholder='Message…'
                            required
                        />
                        <Button type='submit' color='green'>Submit</Button>
                    </Form>            
                </div>
            </div>
        </div>        
    );
}

export default Contacts;